.brc_card {
  margin-bottom: 1rem;
}
.brcc_inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.brcc_inner > img {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.brcci_text {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.gsf_star_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 0.3rem;
}
.gsf_star_container > img {
  width: 0.9rem;
  height: 0.9rem;
}

.brcci_text > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.8);
}
.brc_card > h6 {
  font-weight: 400;
  font-size: 13px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.7);
  white-space: break-spaces;
}
